"use client"

import type React from "react"

import { useEffect, useState } from "react"
import Link from "next/link"
import Map from "@/components/map"
import { useRouter, useParams } from "next/navigation"

interface Document {
  _id: string
  fileName: string
  fileUrl: string
  documentType: string
}

interface Business {
  _id: string
  name: string
  description: string
  category?: string
  status: string
  latitude: number
  longitude: number
  minInvestmentAmount: number
  maxInvestmentAmount: number
  expectedReturnPercentage?: number
  documents: Document[]
  ownerId: { name: string; email: string }
  createdAt: string
}

export default function InvestorBusinessDetailPage() {
  const router = useRouter()
  const params = useParams()
  const businessId = params.id as string

  const [business, setBusiness] = useState<Business | null>(null)
  const [loading, setLoading] = useState(true)
  const [investing, setInvesting] = useState(false)
  const [investmentAmount, setInvestmentAmount] = useState("")
  const [invested, setInvested] = useState(false)
  const [confirmStep, setConfirmStep] = useState(false)
  const [onWatchlist, setOnWatchlist] = useState(false)

  useEffect(() => {
    fetchBusiness()
  }, [businessId])

  const fetchBusiness = async () => {
    try {
      const response = await fetch(`/api/businesses/${businessId}`)
      if (response.ok) {
        const data = await response.json()
        setBusiness(data.data)
      }
    } catch (error) {
      console.error("Failed to fetch business:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleInvest = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!investmentAmount || !business) return
    if (!confirmStep) {
      setConfirmStep(true)
      return
    }
    setInvesting(true)
    try {
      const amount = Number.parseFloat(investmentAmount)
      const response = await fetch("/api/investments", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          businessId,
          amount,
        }),
      })

      if (response.ok) {
        setInvested(true)
        setInvestmentAmount("")
        setConfirmStep(false)
        setTimeout(() => {
          router.push("/investor/investments")
        }, 2000)
      }
    } catch (error) {
      console.error("Failed to create investment:", error)
    } finally {
      setInvesting(false)
    }
  }

  const toggleWatchlist = async () => {
    try {
      if (!onWatchlist) {
        const res = await fetch("/api/watchlist", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ businessId }),
        })
        if (res.ok) setOnWatchlist(true)
      } else {
        const res = await fetch(`/api/watchlist?businessId=${businessId}`, { method: "DELETE" })
        if (res.ok) setOnWatchlist(false)
      }
    } catch {}
  }

  if (loading) {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>
  }

  if (!business) {
    return <div className="min-h-screen flex items-center justify-center">Business not found</div>
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-4xl mx-auto px-4 py-4">
          <Link href="/investor/businesses" className="text-blue-600 hover:text-blue-700 text-sm mb-2 block">
            ‚Üê Back
          </Link>
          <h1 className="text-2xl font-bold text-gray-900">{business.name}</h1>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-4xl mx-auto px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Business Details */}
          <div className="lg:col-span-2 space-y-6">
            {/* Description */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Description</h2>
              <p className="text-gray-600">{business.description}</p>
            </div>

            {/* Business Info */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Business Information</h2>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <p className="text-sm text-gray-600">Business Owner</p>
                  <p className="text-gray-900 font-medium">{business.ownerId.name}</p>
                </div>
                {business.category && (
                  <div>
                    <p className="text-sm text-gray-600">Category</p>
                    <p className="text-gray-900 font-medium">{business.category}</p>
                  </div>
                )}
                <div>
                  <p className="text-sm text-gray-600">Min Investment</p>
                  <p className="text-gray-900 font-medium">${business.minInvestmentAmount.toLocaleString()}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Max Investment</p>
                  <p className="text-gray-900 font-medium">${business.maxInvestmentAmount.toLocaleString()}</p>
                </div>
                {business.expectedReturnPercentage && (
                  <div>
                    <p className="text-sm text-gray-600">Expected Return</p>
                    <p className="text-gray-900 font-medium text-green-600">{business.expectedReturnPercentage}%</p>
                  </div>
                )}
              </div>
            </div>

            {/* Location */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Location</h2>
              <div className="text-gray-600 mb-4">
                Latitude: {business.latitude}, Longitude: {business.longitude}
              </div>
              <Map latitude={business.latitude} longitude={business.longitude} />
            </div>

            {/* Documents */}
            <div className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Documents ({business.documents.length})</h2>
              {business.documents.length === 0 ? (
                <p className="text-gray-600">No documents available</p>
              ) : (
                <div className="space-y-3">
                  {business.documents.map((doc) => (
                    <div
                      key={doc._id}
                      className="flex items-center justify-between p-3 border border-gray-200 rounded-lg"
                    >
                      <div>
                        <p className="font-medium text-gray-900">{doc.fileName}</p>
                        <p className="text-sm text-gray-600 capitalize">{doc.documentType}</p>
                      </div>
                      <a
                        href={doc.fileUrl}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-blue-600 hover:text-blue-700 font-medium text-sm"
                      >
                        Download
                      </a>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* Investment Sidebar */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-sm p-6 sticky top-20">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Invest in This Business</h2>

              {invested ? (
                <div className="bg-green-50 border border-green-200 text-green-700 px-4 py-3 rounded-lg text-sm mb-4">
                  Investment successful! Redirecting...
                </div>
              ) : (
                <form onSubmit={handleInvest} className="space-y-4">
                  <div>
                    <label htmlFor="amount" className="block text-sm font-medium text-gray-700 mb-2">
                      Investment Amount
                    </label>
                    <input
                      id="amount"
                      type="number"
                      value={investmentAmount}
                      onChange={(e) => setInvestmentAmount(e.target.value)}
                      min={business.minInvestmentAmount}
                      max={business.maxInvestmentAmount}
                      step="0.01"
                      placeholder="Enter amount"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      required
                    />
                    <p className="text-xs text-gray-500 mt-1">
                      Between ${business.minInvestmentAmount.toLocaleString()} and $
                      {business.maxInvestmentAmount.toLocaleString()}
                    </p>
                  </div>

                  {!confirmStep ? (
                    <button
                      type="submit"
                      disabled={investing || !investmentAmount}
                      className="w-full bg-green-600 hover:bg-green-700 disabled:bg-gray-400 text-white font-medium py-2 px-4 rounded-lg transition"
                    >
                      {investing ? "Processing..." : "Invest Now"}
                    </button>
                  ) : (
                    <div className="space-y-3">
                      <div className="bg-yellow-50 border border-yellow-200 text-yellow-800 px-4 py-3 rounded-lg text-sm">
                        Please confirm your investment of ${Number.parseFloat(investmentAmount).toLocaleString()} in {business.name}.
                      </div>
                      <div className="flex gap-3">
                        <button
                          type="submit"
                          disabled={investing}
                          className="flex-1 bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded-lg transition"
                        >
                          Confirm Investment
                        </button>
                        <button
                          type="button"
                          onClick={() => setConfirmStep(false)}
                          className="flex-1 bg-gray-200 hover:bg-gray-300 text-gray-900 font-medium py-2 px-4 rounded-lg transition"
                        >
                          Cancel
                        </button>
                      </div>
                    </div>
                  )}
                </form>
              )}

              {/* Investment Summary */}
              <div className="mt-6 pt-6 border-t border-gray-200">
                <p className="text-xs text-gray-600 mb-4">Investment Summary:</p>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-600">Min Investment:</span>
                    <span className="font-medium">${business.minInvestmentAmount.toLocaleString()}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Max Investment:</span>
                    <span className="font-medium">${business.maxInvestmentAmount.toLocaleString()}</span>
                  </div>
                  {business.expectedReturnPercentage && (
                    <div className="flex justify-between pt-2 border-t border-gray-200">
                      <span className="text-gray-600">Expected Return:</span>
                      <span className="font-medium text-green-600">{business.expectedReturnPercentage}%</span>
                    </div>
                  )}
                </div>
                <div className="mt-6 flex gap-3">
                  <button
                    type="button"
                    onClick={toggleWatchlist}
                    className={`flex-1 ${onWatchlist ? "bg-blue-600 hover:bg-blue-700 text-white" : "bg-white border border-blue-600 text-blue-600 hover:bg-blue-50"} font-medium py-2 px-4 rounded-lg transition`}
                  >
                    {onWatchlist ? "Remove from Watchlist" : "Add to Watchlist"}
                  </button>
                  <Link href={`/investor/businesses/${businessId}/chat`} className="flex-1 bg-white border border-gray-300 text-gray-900 hover:bg-gray-50 font-medium py-2 px-4 rounded-lg text-center">
                    Ask a Question
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  )
}
